html, body {
  height: 100%;
  font-family: 'Roboto';
  font-weight: 300; }

.all-wrapper {
  min-height: 100%;
  margin-bottom: -142px; }

.all-wrapper::after {
  content: "";
  display: block; }

.site-footer, .all-wrapper::after {
  height: 142px; }

.left-bg {
  position: fixed;
  width: 50%;
  height: 100%;
  background: #0c3557;
  top: 0;
  left: 0; }

.right-bg {
  position: fixed;
  width: 50%;
  height: 100%;
  background: url(../img/right-bg.jpg) top center no-repeat;
  background-size: cover;
  right: 0;
  top: 0; }

.container {
  max-width: 1140px;
  padding: 0 15px;
  margin: auto; }

.main-content,
.site-header {
  position: relative; }

.site-header {
  padding-top: 60px; }
  .site-header .logo {
    float: left; }
    .site-header .logo a {
      display: block; }
    .site-header .logo img {
      max-width: 100%;
      height: auto; }
  .site-header .language {
    float: right;
    color: #fff; }
    .site-header .language p {
      margin: 0 20px 0 0;
      display: inline-block;
      font-weight: 700;
      font-size: 16px;
      letter-spacing: 1px;
      text-transform: uppercase; }
    .site-header .language__list {
      display: inline-block;
      vertical-align: middle;
      margin: 0;
      padding: 0; }
      .site-header .language__list li {
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 10px; }
        .site-header .language__list li a {
          color: #fff;
          text-decoration: none;
          font-weight: 700; }
          .site-header .language__list li a:hover, .site-header .language__list li a.active {
            border-bottom: 1px solid #fff; }

.left-side {
  width: 50%;
  padding-right: 60px;
  padding-top: 70px;
  position: relative;
  float: left; }
  .left-side h1 {
    color: #fff;
    font-size: 30px;
    font-family: 'Gotham Pro';
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 40px;
    margin: 0 0 55px; }
  .left-side p {
    color: #fff;
    font: 100 16px/22px 'Roboto'; }
  .left-side img {
    display: block;
    float: right;
    margin-top: 40px; }

.right-side {
  float: right;
  width: 50%;
  padding-left: 60px;
  padding-top: 70px;
  position: relative; }

.form-inner {
  width: 100%;
  max-width: 433px; }
  .form-inner p {
    font-size: 18px;
    line-height: 27px;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Gotham Pro';
    margin: 0 0 10px; }
  .form-inner p.note {
    text-align: right;
    font-size: 12px; }
  .form-inner input,
  .form-inner .select {
    display: block;
    width: 100%;
    height: 46px;
    border: 1px solid #e1e1e1;
    border-radius: 3px;
    outline: 0;
    padding: 0 15px;
    font-size: 16px; }
    .form-inner input.err,
    .form-inner .select.err {
      border-color: red; }
  .form-inner .ol-err {
    color: #fff;
    padding-left: 15px;
    display: block;
    margin: 5px 0 15px; }
  .form-inner .select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    cursor: pointer;
    padding-right: 25px;
    background: url(../img/arr-d.png) right 10px center no-repeat #fff; }
  .form-inner input.terms {
    width: 20px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px; }
  .form-inner .terms-label {
    color: #fff;
    margin-bottom: 15px; }
    .form-inner .terms-label a {
      color: #fff; }
  .form-inner .form-row .code {
    width: 55px;
    text-align: center;
    padding: 0 5px;
    float: left; }
  .form-inner .form-row .number {
    margin-left: 70px;
    width: calc(100% - 70px); }
  .form-inner input.submit {
    background: #5887b5;
    color: #fff;
    cursor: pointer;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 700;
    border: none;
    text-transform: uppercase; }
    .form-inner input.submit:hover {
      background: #0f395a; }
  .form-inner .errors {
    color: red; }

.awards-block {
  background-image: linear-gradient(to top, #061522 0%, transparent 100%); }

.our-awards {
  list-style-type: none;
  margin: 0;
  padding: 70px 0 30px;
  text-align: center;
  color: #fff; }
  .our-awards li {
    display: inline-block;
    text-align: left;
    vertical-align: middle;
    max-width: 360px;
    margin: 0 30px; }
    .our-awards li .logo-side {
      float: left; }
      .our-awards li .logo-side img {
        max-width: 100%;
        height: auto; }
    .our-awards li .txt-side {
      margin-left: 185px; }
      .our-awards li .txt-side .heading {
        margin: 0 0 5px;
        font-weight: 400;
        font-family: 'Gotham Pro';
        text-transform: uppercase;
        font-size: 16px;
        letter-spacing: 2px;
        line-height: 22px; }
      .our-awards li .txt-side p {
        font-size: 14px;
        margin: 0; }
    .our-awards li:last-child .txt-side {
      margin-left: 200px; }

.footer-top {
  padding: 15px 0;
  margin-bottom: 20px;
  border-bottom: 1px solid #fff; }
  .footer-top p {
    margin: 0; }
  .footer-top .copyright {
    float: left;
    color: #9ba9b6;
    font-size: 12px; }
  .footer-top .made-by {
    float: right;
    font-size: 12px;
    color: #fff; }
    .footer-top .made-by a {
      color: #5887b5;
      text-decoration: none; }

.site-footer {
  position: relative;
  background: #061522; }

.footer-bottom {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between; }
  .footer-bottom .bottom-txt {
    flex: 1;
    padding-right: 50px;
    margin: 0;
    color: #9ba9b6;
    font-size: 12px;
    font-weight: 300; }
  .footer-bottom .sert {
    font-weight: 300;
    font-size: 14px;
    padding-left: 44px;
    background: url(../img/gerb.png) top 5px left no-repeat;
    padding-top: 3px;
    margin: 0 20px 0 0;
    width: 310px;
    color: #9ba9b6; }

.errors {
  color: red;
  margin-top: 5px; }

@media screen and (max-width: 1000px) {
  .site-footer {
    height: auto; }
  .our-awards li {
    margin: 0 5px;
    max-width: 340px; }
  .our-awards li .txt-side {
    margin-left: 165px; }
  .our-awards li:last-child .txt-side {
    margin-left: 185px; } }

@media screen and (max-width: 767px) {
  .site-header {
    padding-top: 20px; }
  .left-side, .right-side {
    float: none;
    width: 100%;
    padding: 0; }
  .left-side img {
    float: none;
    margin: 0 auto 20px; }
  .left-side {
    margin-top: 20px; }
  .form-inner {
    margin: auto; }
  .our-awards li {
    margin-bottom: 40px; }
  .footer-top .copyright,
  .footer-top .made-by,
  .footer-bottom {
    float: none;
    text-align: center;
    margin-bottom: 5px;
    display: block; }
  .footer-bottom .sert {
    margin: 10px auto 0;
    text-align: left;
    width: auto; }
  .footer-bottom .bottom-txt {
    padding-right: 0; }
  .our-awards li .logo-side {
    float: none; }
  .our-awards li .logo-side img {
    display: block;
    margin: 0 auto 15px; }
  .our-awards li .txt-side {
    margin: 0 !important; }
  .left-side h1 {
    font-size: 23px;
    margin-bottom: 25px; }
  .site-header .language p {
    display: none; }
  .site-header .language__list {
    margin-top: 20px; }
  .terms-label {
    font-size: 13px; }
  .form-inner input.submit {
    font-size: 13px; }
  .all-wrapper {
    margin-bottom: 0; }
  .all-wrapper:after {
    display: none; }
  .footer-bottom {
    margin-bottom: 0;
    padding-bottom: 10px; } }
